@import '../../variables.scss';

.task-input{
    width: 100%;
    height: 3.6rem;
    background-color: #fff;
    color: $very-dark-grayish-blue;
    border-radius: 0.3rem;
    // border-bottom: 1px solid $very-light-grayish-blue;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 1.2rem;
    padding: 0 1.3rem;

    .task-item-mark{
        border-radius: 50%;
        width: 1.3rem;
        height: 1.3rem;
        aspect-ratio: 1;
        border: 1px solid $very-light-grayish-blue;
    }
    
    input{
        width: 100%;
        outline: none;
    }
}

@media(max-width: 600px){
    .task-input{
        height: 2.5rem;

        .task-item-mark{
            width: 1.1rem;
            height: 1.1rem;
        }

        input{
            height: 1rem;
            font-size: 0.7rem;
        }
    }

}